import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import {observer,inject} from 'mobx-react';

import './infinityScroller.scss';

@inject('Commons')
class InfinityScroller extends React.Component {

    constructor(props){
        super();
        this.id = props.Commons.randomId("infinityScroller");
        this.state = {
             scrollerHeight:0,
        }
    }

    onScroll(scrollTop){
        var currentPage = Math.floor(scrollTop/this.containerHeight);
        this.props.onScroll({
            "offset":currentPage*this.limit,
            "limit":this.limit ,
            "currentPage":currentPage,
            "totalPages":this.totalPages
        });
    }

    componentDidMount(){
        this.containerHeight = document.getElementById(this.props.id).getBoundingClientRect().height;
        this.limit = Math.floor(this.containerHeight/this.props.rowHeight);
        this.totalPages = Math.ceil(this.props.data.length/this.limit);
        this.setState({"scrollerHeight":this.totalPages*this.containerHeight});
        this.onScroll(0);
        document.getElementById(this.id).addEventListener("scroll", function(event){
            this.onScroll(event.target.scrollTop);
        }.bind(this));
        
    }

    render() {
        return <div id={this.id} className="comp infinity-scroller">
                    <div style={{"height":this.state.scrollerHeight}}></div>
               </div>
    }

}
export default InfinityScroller;


