import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import {observer,inject} from 'mobx-react';

import InfinityScroller from './../infinityScroller/infinityScroller';
import './dataTable.scss';

@inject('Commons')
class DataTable extends React.Component {

    constructor(props){
        super();
        this.id = props.Commons.randomId("dataTable");
        this.rowHeight = 42;
        this.state = {
             data:props.data,
             slicedData:[]
        }
    }

    onScroll(info){
        var slicedData = this.state.data.slice(info.offset,info.offset + info.limit);
        this.setState({"slicedData":slicedData});
    }

    onInputChange(event,row,col){
        row[col.key] = event.target.value;
        this.forceUpdate();
    }

    rednerTableRow(row,rowIndex){
        var tds = this.props.cols.map((col,index)=>{
            return <td key={rowIndex + index}>
                        { col.editable ? <input value={row[col.key]} onChange={(event)=>this.onInputChange(event,row,col)}/> : <span>{row[col.key]}</span> }
                   </td>
        },this);
        return tds;
    }

    renderThead(){
        var tds = this.props.cols.map((col,index)=>{
            return <td key={index}>{col.header}</td>
        }); 
        return <tr>{tds}</tr>
    }

    renderTbody(){
      var rows = this.state.slicedData.map((row,index)=>{
           return <tr key={index}>{this.rednerTableRow(row,index)}</tr>
      },this)
      return rows;
    }

    render() {
        return <div id={this.id} className="comp data-table">
                    <table>
                        <thead>{this.renderThead()}</thead>
                        <tbody>{this.renderTbody()}</tbody>
                    </table>
                    <InfinityScroller id={this.id} rowHeight={this.rowHeight} onScroll={this.onScroll.bind(this)} data={this.state.data} />
               </div>
    }

}
export default DataTable;


