class Commons{
    
    constructor(){
        this.randomIds = {};
    }

    randomId(str){
        var _randomId = str + "-" + Math.floor((Math.random() * 100000));
        if(!this.randomIds[_randomId]){
            this.randomIds[_randomId] = true;
            return _randomId;
        }
        this.randomId();
    }


}

export default new Commons();


