
import React, { Component } from 'react';
import ReactDOM from 'react-dom';

import './home.scss';
import ToDo from './../toDo/toDo';
import SimplePie from './../simplePie/simplePie';
import Menu from './../menu/menu';
import DataTable from './../dataTable/dataTable'; 
import InfinityScroller from './../infinityScroller/infinityScroller';


class Home extends React.Component {

    constructor(){
      super();

      this.tableData = [];
      
      for(var i=0 ; i <= 1000 ; i++){
        this.tableData.push({name:"elad" + i,age:i})
      }

  
      this.state = {
         SimplePieData : 75.923,
         tableData : []
      }

      this.menuData = [{
        text: "Managing Director",
        items: [
          {
            text: "Sales Director"      
          }, 
          {
            text: "IT Director",
            items: [
              {
                text: "Technical Lead",
                items: [
                  {
                    text: "Software Developer"
                  },
                  {
                    text: "Support Technician"
                  }
                ]
              }        
            ]
          }
        ]
      }];


      this.tableCols  = [
          {header:'Name',key:'name',editable:true},
          {header:'Age',key:'age'}
      ]


    }


    render() {
      return (
        <div className="page home">
            <DataTable data={this.tableData} cols={this.tableCols} /> 
            <Menu data={this.menuData} />
            <ToDo /> 
            <SimplePie data={this.state.SimplePieData} options={{color:"green",hole:120 }}/>

            <button onClick={()=>this.setState({"SimplePieData":50})}>update</button>
        </div>
      )
    }
}


export default Home;


