
import React, { Component } from 'react';
import ReactDOM from 'react-dom';
import {observer,inject} from 'mobx-react';

import './toDo.scss'

@inject('toDoStore') @observer
class ToDo extends React.Component {


    renderTasks(){ 
        var tasks =  this.props.toDoStore.filtered.map(function(task,index){
            return <li key={index}>
                        <span>{task}</span>
                        <span className="delete-task" onClick={()=>this.props.toDoStore.removeTask(index)}>X</span>
                    </li>
        },this)
        return tasks;
    }

    filter(){
        this.props.toDoStore.filter = this.refs.searchRef.value;
    }

    render() {
        return (
            <div className="comp todo-list">
                <div>
                    <span>Search</span>
                    <input ref="searchRef" onChange={()=>this.filter()}/>
                </div>
                <div className="list-container">
                    <ul>
                        <li>
                            <input type="text" ref="newTaskRef" />
                            <button onClick={()=> this.props.toDoStore.addTask(this.refs.newTaskRef.value)}>Add task</button>
                        </li>
                        {this.renderTasks()}
                    </ul>
                </div>
            </div>
        )
    }
}


export default ToDo;



