import React, { Component } from 'react';
import ReactDOM from 'react-dom';

class MenuNode extends React.Component {
    
      render() {      
    
        var childnodes = null;
    
        if(this.props.children) {      
            childnodes = this.props.children.map( (child,index) => <MenuNode key={index} node={child} children={child.items} />);
        }
    
        return (
          <li key={this.props.node.text}>      
            <span>{this.props.node.text}</span>        
            { childnodes ? <ul>{childnodes}</ul> : null }
          </li>
        );
      }
    }

    export default MenuNode;


