import React, { Component } from 'react';
import ReactDOM from 'react-dom';

import './simplePie.scss'

class SimplePie extends React.Component {

    constructor(props){
        super();
        this.id = "canvasComp";
        this.color =  props.options.color || "green";
        this.track =  props.options.track || "#DCDCDC";
        this.hole =  props.options.hole || 0;
        this.state = {
            data : props.data
        }
    }

    drawPieSlice(radius, startAngle, endAngle, color ){
        this.ctx.fillStyle = color;
        this.ctx.beginPath();
        this.ctx.moveTo(this.radius,this.radius);
        this.ctx.arc(this.radius, this.radius, radius, startAngle, endAngle);
        this.ctx.closePath();
        this.ctx.fill();
    }

    animation(startAngle,endAngle,i){
        setTimeout(()=>{
            this.drawPieSlice( this.radius, startAngle, i , this.color );
            if(i <= endAngle ){
                var skip =  endAngle - i > 1  ? 0.5 :0.1;
                i+=skip;
                this.animation(startAngle,endAngle,i);
            }
        },50)
    }

    draw(){
        var startAngle = -Math.PI/2;
        this.radius = this.canvas.width / 2;
        var sliceAngle = 2 * Math.PI * this.props.data / 100;
        this.drawPieSlice( this.radius,0,2 * Math.PI,this.track );
        this.animation(startAngle,startAngle + sliceAngle,startAngle);
    }
    
    componentDidMount(){
        this.element = document.getElementById(this.id);
        this.canvas = this.element.querySelector('canvas');
        this.canvas.width = this.element.getBoundingClientRect().width;
        this.canvas.height = this.element.getBoundingClientRect().height;
        this.ctx = this.canvas.getContext("2d");
        this.draw();
    }

    componentDidUpdate(){
        if(this.props.data != this.state.data){
             this.ctx.clearRect(0, 0,this.canvas.width, this.canvas.height);
             this.setState({"data":this.props.data});
             this.draw()
        }
    }

    render() {
          return (
            <div id={this.id} className="comp pie-chart">
                <canvas />
                <div style={{height:this.hole + "px",width:this.hole+ "px",lineHeight:this.hole }}></div>
                <span>{String(this.state.data).length > 2 && this.state.data < 100 ? this.state.data.toFixed(2) : this.state.data }%</span>
            </div>
          )
        }
    }
    
    
export default SimplePie;


